import { Component, h, Element, State, Host } from '@stencil/core';
import { ICellRendererComp } from 'ag-grid-community';

/**
 * @category Table Cell
 * @description A checkbox cell component for table row selection and boolean data display. Provides accessible selection controls within data tables.
 */
@Component({
    tag: 'atui-checkbox-cell',
    shadow: false,
})
export class AtuiCheckboxCellComponent implements ICellRendererComp {
    @Element() el;
    @State() params;

    init(params): void {
        this.params = params;
        this.params.api.refreshHeader();
        this.params.width = 60;
    }

    getGui(): HTMLAtuiCheckboxCellElement {
        return this.el;
    }

    refresh(params): boolean {
        this.params = params;
        return true;
    }

    setValue(checked: boolean): void {
        if (checked !== undefined) {
            this.params.setValue(checked);
            this.params.api.refreshHeader();
        }
    }

    render() {
        return (
            <Host class="flex h-full items-center">
                <atui-checkbox
                    class="w-auto self-center"
                    disabled={
                        this.params.getDisabled
                            ? this.params.getDisabled(this.params.data)
                            : false
                    }
                    value={this.params.value}
                    onAtuiChange={(event: CustomEvent) =>
                        this.setValue(event.detail)
                    }
                ></atui-checkbox>
            </Host>
        );
    }
}
